.navbar-brand img{
    height: 80px;
    width: 110px;
}
.navbar-toggler{
   outline: none !important;
   border: none !important;
}
#navbar-nav{
    background-color: white;
    position: sticky;
    top: 0;
    z-index: 10;
}
.navbar-nav{
    font-size: 16px;
    font-weight: 600;
}

@media (max-width: 1440px) {
    .nav-item .nav-link{
        font-size: 16px;
    }
    .navbar-brand img {
        height: 60px;
        width: 60px;
    }
}

@media (max-width: 550px) {
    .navbar-brand img{
        height: 40px;
        
    }
}